import React from 'react';
import QRCode from 'react-qr-code';
import {
  IconButton,
  Dialog,
  DialogActions,
  DialogContent,
  Link,
  SvgIcon,
  Tooltip,
  Zoom,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  useMediaQuery,
  SwipeableDrawer,
  TextField,
} from '@material-ui/core';
// import {
//   Language as LanguageIcon,
//   Bookmark as BookmarkIcon,
//   Share as ShareIcon,
//   ExpandMore as ExpandMoreIcon,
//   Phone as PhoneIcon,
//   Email as EmailIcon,
//   Message as MessageIcon,
//   QrCode2 as QrCode2Icon,
//   Cancel as CancelIcon,
//   AddCircle as AddCircleIcon,
// } from '@material-ui/icons';
import {
  phoneIcon,
  messageIcon,
  emailIcon,
  qrCodeIcon,
  expandMoreIcon,
  bookmarkIcon,
  folderAccountIcon,
  folderAddIcon,
  addIcon,
} from '../../assets/svg/SvgIcons';
import { LoadingButton } from '@material-ui/lab';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import { useWindowSize } from '../../hooks/useWindowSize';
import CustomDialogTitle from './CustomDialogTitle';

import clsx from 'clsx';

const useStyles = makeStyles((theme) => ({
  expand: {
    transform: 'rotate(0deg)',
    marginLeft: 'auto',
    transition: theme.transitions.create('transform', {
      duration: theme.transitions.duration.shortest,
    }),
  },
  expandOpen: {
    transform: 'rotate(180deg)',
  },
}));

const CardActionLinks = (props) => {
  const {
    data,
    bookmarkIsOpen,
    shareIsOpen,
    qrCodeIsOpen,
    bookmarkHandler,
    shareHandler,
    qrCodeHandler,
    expanded,
    handleExpandClick,
    bookmarkList,
    isCreationMode,
    newNameCardHandler,
  } = props;
  const classes = useStyles();

  const theme = useTheme();
  const isSmallDevice = useMediaQuery(theme.breakpoints.down('sm'));
  const isMediumDevice = useMediaQuery(theme.breakpoints.up('sm'));

  return (
    <>
      {/* <Link
        href={data.website}
        underline="none"
        target="_blank"
        rel="noopener"
        component="a"
      >
        <IconButton aria-label="website">
          <SvgIcon>
            <path d={earthIcon.path} />
          </SvgIcon>
        </IconButton>
      </Link> */}
      <Tooltip arrow TransitionComponent={Zoom} title="Bookmark">
        <IconButton aria-label="bookmark" onClick={bookmarkHandler}>
          <SvgIcon>
            <path d={bookmarkIcon.path} />
          </SvgIcon>
        </IconButton>
      </Tooltip>

      <Dialog
        open={bookmarkIsOpen}
        aria-labelledby="bookmark-dialog-title"
        onClose={bookmarkHandler}
        fullWidth
        maxWidth="xs"
      >
        <CustomDialogTitle
          id="bookmark-dialog-title"
          onClose={bookmarkHandler}
          svgIcon={bookmarkIcon.path}
          isCreationMode={isCreationMode}
          newNameCardHandler={newNameCardHandler}
          // onAdd={bookmarkAddHandler}
        >
          Bookmark
        </CustomDialogTitle>
        <DialogContent dividers>
          {isCreationMode ? (
            <TextField
              autoFocus
              margin="dense"
              id="folder-name"
              label="Folder Name"
              type="text"
              fullWidth
              variant="standard"
              color="secondary"
            />
          ) : (
            <List>
              {bookmarkList.map((bookmarkItem) => (
                <ListItem button>
                  <ListItemIcon>
                    <SvgIcon>
                      <path d={folderAccountIcon.path} />
                    </SvgIcon>
                  </ListItemIcon>
                  <ListItemText primary={bookmarkItem} />
                </ListItem>
              ))}
              <ListItem button onClick={newNameCardHandler}>
                <ListItemIcon>
                  <SvgIcon>
                    <path d={folderAddIcon.path} />
                  </SvgIcon>
                </ListItemIcon>
                <ListItemText primary="New Card Holder" />
              </ListItem>
            </List>
          )}
        </DialogContent>
        {isCreationMode ? (
          <DialogActions>
            <LoadingButton
              pendingPosition="start"
              color="secondary"
              startIcon={
                <SvgIcon>
                  <path d={addIcon.path} />
                </SvgIcon>
              }
              onClick={newNameCardHandler}
            >
              Create
            </LoadingButton>
          </DialogActions>
        ) : null}
      </Dialog>

      <Link
        href={`tel:+${data.contactPerson.contactNumber}`}
        underline="none"
        target="_self"
        rel="noopener"
        component="a"
      >
        <Tooltip arrow TransitionComponent={Zoom} title="Call">
          <IconButton aria-label="call">
            <SvgIcon>
              <path d={phoneIcon.path} />
            </SvgIcon>
          </IconButton>
        </Tooltip>
      </Link>
      <Link
        href={`mailto:${data.contactPerson.contactEmail}`}
        underline="none"
        target="_self"
        rel="noopener"
        component="a"
      >
        <Tooltip arrow TransitionComponent={Zoom} title="Email">
          <IconButton aria-label="email">
            <SvgIcon>
              <path d={emailIcon.path} />
            </SvgIcon>
          </IconButton>
        </Tooltip>
      </Link>
      <Link
        href={`sms:/${data.contactPerson.contactNumber}/?body=/${data.contactPerson.smsBody}/`}
        underline="none"
        target="_self"
        rel="noopener"
        component="a"
      >
        <Tooltip arrow TransitionComponent={Zoom} title="Message">
          <IconButton aria-label="message">
            <SvgIcon>
              <path d={messageIcon.path} />
            </SvgIcon>
          </IconButton>
        </Tooltip>
      </Link>
      {/* <IconButton aria-label="share" onClick={shareHandler}>
        <SvgIcon>
          <path d={shareIcon.path} />
        </SvgIcon>
      </IconButton>
      <Dialog
        open={shareIsOpen}
        aria-labelledby="share-dialog-title"
        onClose={shareHandler}
      >
        <CustomDialogTitle id="share-dialog-title" onClose={shareHandler}>
          Share
        </CustomDialogTitle>
        <DialogContent dividers>
          <QRCode value={data.initLink} />
        </DialogContent>
      </Dialog> */}
      <Tooltip arrow TransitionComponent={Zoom} title="QR Code">
        <IconButton aria-label="qr-code" onClick={qrCodeHandler}>
          <SvgIcon>
            <path d={qrCodeIcon.path} />
          </SvgIcon>
        </IconButton>
      </Tooltip>
      <Dialog
        open={qrCodeIsOpen}
        aria-labelledby="qr-code-dialog-title"
        onClose={qrCodeHandler}
      >
        <CustomDialogTitle
          svgIcon={qrCodeIcon.path}
          id="qr-code-dialog-title"
          onClose={qrCodeHandler}
          isCreationMode={isCreationMode}
        >
          QR Code
        </CustomDialogTitle>
        <DialogContent dividers>
          <QRCode value={data.initLink} />
        </DialogContent>
      </Dialog>
      <IconButton
        className={clsx(classes.expand, {
          [classes.expandOpen]: expanded,
        })}
        onClick={handleExpandClick}
        aria-expanded={expanded}
        aria-label="show more"
      >
        <SvgIcon>
          <path d={expandMoreIcon.path} />
        </SvgIcon>
      </IconButton>
    </>
  );
};

export default CardActionLinks;
