import React, { useState } from 'react';
import {
  IconButton,
  DialogTitle,
  Typography,
  SvgIcon,
  Box,
  Tooltip,
  Zoom,
} from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';

import { closeIcon, addIcon, backIcon } from '../../assets/svg/SvgIcons';

const useStyles = makeStyles((theme) => ({
  dialogHeader: {
    margin: `0 ${theme.spacing(2)}`,
  },
  iconContainer: { width: 24, height: 24 },
  dialogCloseButton: {
    // position: 'absolute',
    right: theme.spacing(1),
    top: theme.spacing(1),
  },
  dialogAddButton: {
    position: 'absolute',
    right: theme.spacing(1),
    top: theme.spacing(1),
  },
}));

const CustomDialogTitle = (props) => {
  const {
    children,
    svgIcon,
    onClose,
    onAdd,
    isCreationMode,
    newNameCardHandler,
    ...rest
  } = props;
  const classes = useStyles();

  return (
    <DialogTitle disableTypography {...rest}>
      <Box sx={{ display: 'flex', alignItems: 'center' }}>
        {isCreationMode ? (
          <Tooltip arrow TransitionComponent={Zoom} title="Back">
            <IconButton aria-label="back" onClick={newNameCardHandler}>
              <SvgIcon>
                <path d={backIcon.path} />
              </SvgIcon>
            </IconButton>
          </Tooltip>
        ) : null}
        <Box className={classes.iconContainer}>
          <SvgIcon>
            <path d={svgIcon} />
          </SvgIcon>
        </Box>
        <Box className={classes.dialogHeader} style={{ flexGrow: 1 }}>
          <Typography variant="h6" component="div">
            {children}
          </Typography>
        </Box>
        {onAdd ? (
          <Tooltip arrow TransitionComponent={Zoom} title="Add">
            <IconButton
              aria-label="add"
              onClick={onAdd}
              className={classes.dialogAddButton}
            >
              <SvgIcon>
                <path d={addIcon.path} />
              </SvgIcon>
            </IconButton>
          </Tooltip>
        ) : null}
        <Box>
          {onClose ? (
            <Tooltip arrow TransitionComponent={Zoom} title="Close">
              <IconButton
                aria-label="close"
                onClick={onClose}
                // className={classes.dialogCloseButton}
              >
                <SvgIcon>
                  <path d={closeIcon.path} />
                </SvgIcon>
              </IconButton>
            </Tooltip>
          ) : null}
        </Box>
      </Box>
    </DialogTitle>
  );
};

export default CustomDialogTitle;
